a{
    /* background-color: rgb(199, 33, 33); */
    text-decoration: none;
    color: rgb(238, 238, 238);
    transition: 0.2s;
    padding: 0 10px;
    text-transform: capitalize;
    background-color: transparent;
    letter-spacing: 3px;
    /* font-family: 'Poppins',sans-serif; */
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    position: relative;
    transition: 0.3s ease-in-out;
    list-style: none;
}
a::after{
    content: "";
    position: absolute;
    background-color: rgb(20, 140, 220);
    border-radius: 20px;
    height: 5px;
    transition: 0.3s;
    left: 0;
    bottom: -10px;
    width: 0%;

}


.nav-items{
    font-size: 20px;
    padding: 18px;
    /* margin-right: 70px; */
    background-color: #17141d;
    text-decoration: none;
    list-style: none;
}

a:hover{
    color: crimson;
}
a:hover:after{
    /* width: 100%;     */
}   

.active{
    color: rgb(19, 87, 189);
}
.active:hover{
    color: rgb(19, 87, 189);
}
.active::after{
    content: "";
    position: absolute;
    background-color: rgb(19, 87, 189);
    border-radius: 20px;
    height: 5px;
    transition: 0.3s;
    left: 0;
    bottom: -10px;
    width: 100%;
    transition: 0.3s;

}
