.flex_container{
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
  flex-wrap: wrap;
}

.card{
  background-color:#17141d;
  height: 450px;
  width: 400px;
  border-radius: 15px;
  margin: 10px;
  margin-top: -40px;
  transition: 0.3s ease-in-out;
  color: #17141d;
}

.card:hover{
  transform: translateY(-15px);
  /* box-shadow: -1rem 0px 3rem rgb(7, 5, 5); */
  /* color: aliceblue; */

}

.text{
  text-align: center;
  justify-content: center;
  padding: 70px;
  margin-top: 10px;
  font-size: larger;
  transition: 0.5 ease-in;
  position: relative;
}
#card1:hover{
color: white;
}

.headingcard{
  color: white;
  font-size: larger;
  text-align: center;
  justify-content: justify;
  margin-top: 50px;
}

#card3{
 transition: 0.3s;
}
#card3::after{
  content: "";
  position: absolute;
  background-color: rgb(20, 140, 220);
  border-radius: 20px;
  height: 5px;
  transition: 0.3s;
  left: 0;
  bottom: -10px;
  margin-left: 100px;
  width: 0%;
}
#card3:hover::after{
  content: "";
  position: absolute;
  background-color: rgb(20, 140, 220);
  border-radius: 20px;
  height: 5px;
  justify-content: center;
  transition: 0.3s;
  left: 0;
  bottom: -10px;
  margin-left: 100px;
  width: 50%;
}
